@use '@/assets/scss/global.scss' as *;
@use '@/assets/scss/deve.scss' as *;
@use '@/assets/scss/variables.scss' as *;

#articles_comment {
  .comment-top {
    margin-bottom: 12px;

    .comment-title {
      font-size: 19px;
      color: #34495e;
      font-weight: 600;
    }

    .right-bar-wrap {
      float: right;
      position: relative;
      top: 10px;

      .icon {
        font-size: 18px;
        font-weight: 700;
        cursor: pointer;
        padding-left: 4px;
        color: #787878;
      }
    }
  }

  .comment-textarea {
    height: 200px;

    textarea {
      width: 100%;
      height: 100%;
      outline: none;
      resize: none;
      padding: 5px;
      line-height: 18px;
      border: 1px solid rgba(149, 149, 149, .7);
      box-sizing: border-box;
    }

    textarea:focus {
      border: 1px solid rgb(210, 210, 210);
    }
  }

  .comment-bottom {
    position: relative;
    margin-top: 4px;

    .comment-btn {
      position: absolute;
      top: 7px;
      width: 100%;
      text-align: center;

      span {
        vertical-align: top;
        font-size: 14px;
        border: 1px solid hsla(0, 0%, 62.7%, .5);
        padding: 3px 35px;
        border-radius: 5px;
        background-color: hsla(0, 0%, 52.9%, .2);
        cursor: pointer;
      }

      .cancel {
        padding: 3px 8px;
        font-size: 11px;
        vertical-align: bottom;
        margin-left: 5px;
        position: absolute;
      }
    }

    .comment-btn {
      span:hover {
        background-color: rgba(115, 115, 115, .3);
      }

      span:active {
        background-color: rgba(125, 125, 125, .4);
      }

    }

    .opt-bar {
      .ext-wrap {
        >span {
          vertical-align: top;
          line-height: 18px;
          width: 60px;
          display: inline-block;
          text-align: center;
          cursor: pointer;
          border: 1px solid hsla(0, 0%, 62.7%, .5);
          padding: 3px 3px 1px 3px;
          margin: 3px 3px 3px 0;
          border-radius: 5px;
          background-color: hsla(0, 0%, 74.5%, .2);
          font-size: 9px;
          z-index: 1;
          position: relative;
        }

        >span:hover {
          background-color: rgba(115, 115, 115, .3);
        }

        >span:active {
          background-color: rgba(125, 125, 125, .4);
        }

      }
    }
  }
}
